@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,500,700&display=swap);
body{margin:0;padding:0;font-family:'Montserrat', sans-serif;letter-spacing:1px}*{font-family:inherit;box-sizing:border-box;outline:none}

header{width:100%;height:50px;background:linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%)}header div.wrapper{display:flex;align-items:center;justify-content:space-between;position:relative;max-width:1200px;width:100%;height:100%;margin:auto;padding:0 15px}header div.wrapper a.menu-button{display:flex;align-items:center;justify-content:center;width:30px;height:30px;border-radius:50%;background:white;color:linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%);font-size:12px;cursor:pointer;opacity:0.75;-webkit-transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53)}header div.wrapper a.menu-button:hover,header div.wrapper a.menu-button.active{opacity:1}header div.wrapper div.brand{display:flex;align-items:center}header div.wrapper div.brand img{height:32px;margin:0 15px 5px 0}header div.wrapper div.brand h1{color:white;font-size:24px;font-weight:300;letter-spacing:2px;margin:0 0 2.5px 0}header div.wrapper div.menu{position:absolute;right:0;top:50px;width:180px;background:#f2f2f2;overflow:hidden;border-radius:0 5px 5px 0;-webkit-transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);opacity:0;pointer-events:none}header div.wrapper div.menu a.link{display:flex;align-items:center;width:100%;height:25px;padding:0 10px;font-size:12px;text-decoration:none;color:black}header div.wrapper div.menu.active{opacity:1;pointer-events:inherit}footer{width:100%;min-height:180px;background:#f2f2f2}footer div.wrapper{width:100%;max-width:1200px;margin:auto;padding:15px}footer div.fhead{display:flex;align-items:center}footer div.fhead img{height:24px;margin:0 15px 0 0}footer div.menu-items{display:flex;flex-wrap:wrap}footer div.menu-items a{display:flex;align-items:center;width:50%;height:40px;color:black}

div.container{width:100%;min-height:calc(100vh - 230px)}div.page-wrap{width:100%;max-width:1200px;min-height:calc(100vh - 230px);margin:auto;padding:15px}div.page-wrap h2,div.page-wrap h3,div.page-wrap h4,div.page-wrap h5{font-weight:400}

div.page-wrap.page-home{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:15px 15px 90px 15px}div.page-wrap.page-home h2,div.page-wrap.page-home h3{text-align:center}div.page-wrap.page-home h2{font-size:32px}div.page-wrap.page-home h3{max-width:640px;margin:0 auto 45px auto}div.page-wrap.page-home div.banner{display:flex;align-items:center;justify-content:center;width:100%;height:120px;margin:0 0 45px 0}div.page-wrap.page-home div.banner img{height:60px;margin:15px}div.page-wrap.page-home div.banner i{font-size:32px;margin:0 10px;-webkit-animation:chevron;animation:chevron;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear}@-webkit-keyframes chevron{0%{opacity:1}50%{opacity:0}100%{opacity:1}}@keyframes chevron{0%{opacity:1}50%{opacity:0}100%{opacity:1}}div.page-wrap.page-home div.banner i:nth-of-type(1){-webkit-animation-delay:2s;animation-delay:2s}div.page-wrap.page-home div.banner i:nth-of-type(2){-webkit-animation-delay:1s;animation-delay:1s}div.page-wrap.page-home div.banner i:nth-of-type(3){-webkit-animation-delay:0s;animation-delay:0s}div.page-wrap.page-home div.buttons{display:flex;align-items:center;justify-content:center;width:100%;height:90px}div.page-wrap.page-home div.buttons a.button{display:flex;align-items:center;justify-content:center;background:linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%);color:white;width:240px;height:50px;border-radius:5px;margin:0 15px;opacity:0.75;cursor:pointer;font-size:16px;text-decoration:none;-webkit-transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53)}div.page-wrap.page-home div.buttons a.button i{margin:0 10px 0 0}div.page-wrap.page-home div.buttons a.button:hover{opacity:1}

div.page-wrap.page-examples div.example{width:100%}div.page-wrap.page-examples div.example div.example-buttons{display:flex;align-items:center;width:100%;height:45px;margin:0 0 15px 0}div.page-wrap.page-examples div.example div.example-buttons a.example-button{display:flex;align-items:center;justify-content:center;background:linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%);color:white;width:180px;height:30px;border-radius:5px;margin:0 15px 0 0;opacity:0.75;cursor:pointer;font-size:12px;font-weight:bold;text-decoration:none;-webkit-transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53)}div.page-wrap.page-examples div.example div.example-buttons a.example-button i{margin:0 10px 0 0}div.page-wrap.page-examples div.example div.example-buttons a.example-button:hover,div.page-wrap.page-examples div.example div.example-buttons a.example-button.active{opacity:1}div.page-wrap.page-examples div.example-content{display:flex}div.page-wrap.page-examples div.example-content div.example-text{width:calc(100% - 480px);padding:0 30px 0 0}div.page-wrap.page-examples div.example-content div.CodeMirror{width:480px;height:180px}

